<template>
  <div>
    <div class="teams">
      <p>
        Teams
        <span>(optional)</span>
      </p>
      <searchinput
        v-model="getProjectSettings.access.teams"
        placeholdertext="teams"
        @changed="effectMembers"
      ></searchinput>
    </div>
    <div class="users">
      <p>
        Users
        <span>(Optional)</span>
      </p>
      <searchinput
        v-model="getProjectSettings.access.users"
        placeholdertext="users"
        @changed="setSettingsChanged(true)"
      ></searchinput>
    </div>
  </div>
</template>

<script>
import searchinput from "@/components/Input/SearchInput.vue";
import { mapGetters, mapMutations } from "vuex";
export default {
  components: {
    searchinput
  },
  computed: {
    ...mapGetters(["getProjectSettings"])
  },
  methods: {
    ...mapMutations(["setSettingsChanged"]),
    effectMembers(payload) {
      this.setSettingsChanged(true);
      if (payload.members) {
        this.getProjectSettings.access.users.forEach(element => {
          if (payload.members.includes(element.name)) {
            element.selected = payload.selected;
            if (payload.selected) {
              element.disabled = true;
            } else {
              element.disabled = false;
            }
          }
        });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.teams {
  padding: 25px 0;
}
p {
  padding-bottom: 8px;
}
</style>