<template>
  <div class="container">
    <div class="heading">
      <h2>Projects</h2>
    </div>
    <div class="grid-item">
      <img :src="icon.search" />
      <div class="gap"></div>
      <h4>SEARCH</h4>
    </div>
    <div class="grid-item pointer" @click="showFilter">
      <img :src="icon.filter" />
      <div class="gap"></div>
      <h4>FILTER</h4>
    </div>
    <div class="grid-item">
      <img
        class="grid-icon"
        :src="icon.grid"
        @click="setProjectDisplay('grid'); icon.list = icon.showlist; icon.grid = icon.showgridsushi"
      />
      <div class="gap"></div>
      <div class="gap"></div>
      <img
        class="list-icon"
        :src="icon.list"
        @click="setProjectDisplay('list'); icon.list = icon.showlistsushi; icon.grid = icon.showgrid"
      />
    </div>
    <div class="grid-item">
      <ButtonSuccess @clicked="newProject">+ New Project</ButtonSuccess>
      <NewProjectModal v-if="show.modal" @close="show.modal = false" @newcard="newCard"></NewProjectModal>
      <div class="gap"></div>
      <div class="gap"></div>
      <img :src="icon.avatar" height="33px" width="33px" />
    </div>
  </div>
</template>

<script>
import searchicon from "@/assets/search.svg";
import filtericon from "@/assets/filter.svg";
import showgrid from "@/assets/projectbar/show-grid.svg";
import showlist from "@/assets/projectbar/show-list.svg";
import showgridsushi from "@/assets/projectbar/show-grid-sushi.svg";
import showlistsushi from "@/assets/projectbar/show-list-sushi.svg";
import avatar from "@/assets/avatar.png";
import NewProjectModal from "@/components/NewProjectModal.vue";
import ButtonSuccess from "@/components/ButtonSuccess.vue";
import { mapMutations } from "vuex";
export default {
  components: {
    NewProjectModal,
    ButtonSuccess
  },
  data() {
    return {
      icon: {
        search: searchicon,
        filter: filtericon,
        grid: showgridsushi,
        list: showlist,
        avatar: avatar,
        showgridsushi,
        showlistsushi,
        showlist,
        showgrid
      },
      show: {
        modal: false,
        display: "grid"
      }
    };
  },
  methods: {
    ...mapMutations(["setProjectDisplay", "showFilterBar"]),
    newProject() {
      this.show.modal = true;
    },
    newCard(payload) {
      this.$emit("newprojectbutton", payload);
      this.show.modal = false;
    },
    showFilter() {
      this.showFilterBar();
    }
  },
  mounted() {
    this.show.display = this.$store.state.projects.display;
  }
};
</script>

<style lang="scss" scoped>
.pointer {
  cursor: pointer;
}
.container {
  display: grid;
  grid-template-columns: auto 129px 123px 112px 225px;
  grid-template-rows: 70px;
  h2 {
    @include Framewrk-Heading-02-Default-28353D;
    display: flex;
  }
  h4 {
    @include Framewrk-Heading-04-Subdued-7C868C;
  }
  .gap {
    width: 10px;
  }
  .heading {
    display: flex;
    align-items: center;
    padding: 0 23px;
    border-bottom: solid 1px $mercury-40;
  }
  .grid-item {
    display: flex;
    align-items: center;
    justify-content: center;
    border-left: solid 1px $mercury-40;
    border-bottom: solid 1px $mercury-40;
  }
  .list-icon {
    cursor: pointer;
    fill: $mercury-40;
  }
  .grid-icon {
    cursor: pointer;
    fill: $mercury-40;
  }
}
</style>