<template>
  <div class="body-container" :class="{'center-flex': cards.length === 0}">
    <div class="list-bar" v-if="cards.length != 0">
      <div class="center-checkbox">
        <customcheckbox v-model="selectAllBox" @input="selectall"></customcheckbox>
      </div>
      <div class="labels" v-if="getSelected === 0">
        <div class="label" @click="sortListBy('published')">
          Status
          <img
            :src="dropdownsmall"
            :class="{rotate: sortstyleorder === 'desc'}"
            v-if="sortstyle === 'published'"
          />
        </div>
        <div class="label" @click="sortListBy('title')">
          Name
          <img
            :src="dropdownsmall"
            :class="{rotate: sortstyleorder === 'desc'}"
            v-if="sortstyle === 'title'"
          />
        </div>
        <div></div>
        <div class="label" @click="sortListBy('created')">
          Date Created
          <img
            :src="dropdownsmall"
            :class="{rotate: sortstyleorder === 'desc'}"
            v-if="sortstyle === 'created'"
          />
        </div>
        <div></div>
        <div class="label" @click="sortListBy('lastedited')">
          Last Edited
          <img
            :src="dropdownsmall"
            :class="{rotate: sortstyleorder === 'desc'}"
            v-if="sortstyle === 'lastedited'"
          />
        </div>
        <div></div>
        <div class="label" @click="sortListBy('owner')">
          Created By
          <img
            :src="dropdownsmall"
            :class="{rotate: sortstyleorder === 'desc'}"
            v-if="sortstyle === 'owner'"
          />
        </div>
        <div></div>
      </div>
      <div class="selected-label" v-if="getSelected != 0" @click="deleteSelectedCards">
        {{ getSelected }} Selected
        <img :src="deleteicon" />
      </div>
    </div>
    <div class="no-contents" v-if="cards.length === 0">No projects currently</div>
    <div class="list-padding" v-if="cards.length != 0">
      <ListItem class="list-item" v-for="card in filteredCards" :key="card.id" :details="card"></ListItem>
    </div>
  </div>
</template>

<script>
import ListItem from "@/components/ListItem.vue";
import { mapGetters, mapMutations } from "vuex";
import customcheckbox from "@/components/Input/Checkbox.vue";
import dropdownsmall from "@/assets/drop-down-small.svg";
import deleteicon from "@/assets/icon/delete.svg";
export default {
  props: {
    cards: {
      type: Array
    }
  },
  data() {
    return {
      selectAllBox: false,
      selected: [],
      sortstyle: "title",
      sortstyleorder: "asc",
      sortedcardarray: null,
      dropdownsmall,
      deleteicon
    };
  },
  components: { ListItem, customcheckbox },
  computed: {
    ...mapGetters(["getSelected", "getFB"]),
    sortedCards() {
      return this.cards
        .slice()
        .sort(this.compareValues(this.sortstyle, this.sortstyleorder));
    },
    filteredCards() {
      let cards = this.sortedCards;

      if (this.getFB.published.value === "Published") {
        cards = cards.filter(card => card.published);
      } else if (this.getFB.published.value === "Unpublished") {
        cards = cards.filter(card => !card.published);
      }

      if (this.getFB.datecreated.value === "Last 7 days") {
        cards = cards.filter(card =>
          this.$moment(card.created).isAfter(this.$moment().subtract(7, "days"))
        );
      } else if (this.getFB.datecreated.value === "Last month") {
        cards = cards.filter(card =>
          this.$moment(card.created).isAfter(
            this.$moment().subtract(1, "months")
          )
        );
      } else if (this.getFB.datecreated.value === "Last 6 months") {
        cards = cards.filter(card =>
          this.$moment(card.created).isAfter(
            this.$moment().subtract(6, "months")
          )
        );
      } else if (this.getFB.datecreated.value === "Last year") {
        cards = cards.filter(card =>
          this.$moment(card.created).isAfter(
            this.$moment().subtract(12, "months")
          )
        );
      }

      if (this.getFB.dateedited.value === "Last 7 days") {
        cards = cards.filter(card =>
          this.$moment(card.lastedited).isAfter(this.$moment().subtract(7, "days"))
        );
      } else if (this.getFB.dateedited.value === "Last month") {
        cards = cards.filter(card =>
          this.$moment(card.lastedited).isAfter(
            this.$moment().subtract(1, "months")
          )
        );
      } else if (this.getFB.dateedited.value === "Last 6 months") {
        cards = cards.filter(card =>
          this.$moment(card.lastedited).isAfter(
            this.$moment().subtract(6, "months")
          )
        );
      } else if (this.getFB.dateedited.value === "Last year") {
        cards = cards.filter(card =>
          this.$moment(card.lastedited).isAfter(
            this.$moment().subtract(12, "months")
          )
        );
      }

      return cards;
    }
  },
  methods: {
    ...mapMutations(["setSelectedOfAll", "deleteSelectedCards"]),
    selectall() {
      this.setSelectedOfAll(this.selectAllBox);
    },
    itemToSelected(payload) {
      this.selected.push(payload);
    },
    sortListBy(payload) {
      if (this.sortstyle === payload && this.sortstyleorder === "asc") {
        this.sortstyleorder = "desc";
      } else if (this.sortstyle === payload && this.sortstyleorder === "desc") {
        this.sortstyleorder = "asc";
      } else {
        this.sortstyle = payload;
        this.sortstyleorder = "asc";
      }
    },
    compareValues(key, order) {
      return function innerSort(a, b) {
        if (!a.hasOwnProperty(key) || !b.hasOwnProperty(key)) {
          return 0;
        }
        const varA = typeof a[key] === "string" ? a[key].toUpperCase() : a[key];
        const varB = typeof b[key] === "string" ? b[key].toUpperCase() : b[key];
        let comparison = 0;
        if (varA > varB) {
          comparison = 1;
        } else if (varA < varB) {
          comparison = -1;
        }
        return order === "desc" ? comparison * -1 : comparison;
      };
    }
  }
};
</script>

<style lang="scss" scoped>
.body-container {
  background-color: $mercury-20;
  .list-bar {
    height: 50px;
    background-color: $mercury-10;
    border-bottom: solid 1px $mercury-40;
    display: grid;
    grid-template-columns: 70px auto;
    align-items: center;
    .center-checkbox {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-right: 6px;
    }
    .labels {
      display: grid;
      grid-template-columns: 75px 70px auto 110px 20px 100px 30px 90px 200px;
    }
    .label {
      font-family: Heebo;
      font-size: 11.5px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: 0.61px;
      color: $mercury-80;
      text-transform: uppercase;
      cursor: pointer;
      img {
        margin-bottom: 2px;
        margin-left: 5px;
      }
      .rotate {
        transform: rotate(180deg);
      }
    }
    .selected-label {
      font-family: Heebo;
      font-size: 12.5px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      color: $outer-space-30;
      display: flex;
      align-items: center;
      img {
        margin-left: 15px;
      }
    }
  }
  .list-padding {
    padding: 16px 23px;
    .list-item {
      margin-bottom: 10px;
    }
  }
}
.center-flex {
  display: flex;
  align-items: center;
  justify-content: center;
}
.no-contents {
  font-family: Heebo;
  font-size: 20px;
  font-weight: 800;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.3;
  letter-spacing: normal;
  color: $mercury-50;
}
</style>